.container{
    display: flex;
    flex-direction: column;
    .cartItem{
        background-color: rgb(228, 228, 228);
        width: 60%;
        margin: 50px;
        border: 1px solid var(--color-primary);
        padding: 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .cartItem__picture{ 
            padding: 20px;
            img{
                padding-left: 20px;
                max-width: 100%;
                max-height: 250px;
            }
        }
        .cartItem__info{
            padding: 20px;
            vertical-align: top;
            .info__name{
               display: flex;
               font-size: 32px;
               border-bottom: 1px solid black;
            }
            .info__cost{
                display: flex;
                font-size: 24px;
                margin-top: 50px;
            }
            .info__count{
                margin-top: 5px;
                display: flex;
            }
        }
        .cartItem__buttons{
            display: flex;
            flex-direction: column;
            justify-content: center;
            button{
                margin-left: 50px;
                margin: 0 auto;
                border: 1px solid black;
                background-color: var(--color-primary);
                padding: 15px;
                font-size: 20px;
            }
            button:hover{
                border: 1px solid var(--color-primary);
                background-color: black;
                color: var(--color-primary);
            }
        }
        
    }
    .cartWindow{
        position: fixed;
        border: 1px solid var(--color-primary);
        padding: 20px;
        color: white;
        margin: 50px;
        display: inline-block;
        height: fit-content;
        right: 0;
        margin: 10% auto;
        background-color: black;
        margin-right: 20px;
        .cartSum span{
            color: var(--color-primary)
        }
    }
}